/**
 * = Modals
 */

.modal {
    background: rgba(17, 24, 39, 0.5) !important;

    &.static-example {
        position: relative;
        display: block;
    }
}

.modal-backdrop {
    background: none;
}

.modal-content {
    border: 0;
    @if $enable-rounded {
        border-radius: $modal-content-border-radius;
    } @else {
        border-radius: 0;
    }
}

.modal-fluid {
    .modal-dialog {
        margin-top: 0;
        margin-bottom: 0;
    }
    .modal-content {
        border-radius: 0;
    }
}

@each $color, $value in $theme-colors {
    .modal-#{$color} {
        @include modal-variant($value);
    }
}
